<template>
  <div>
    <div class="effects" :class="{expanded}">
      <a @click="effectsPressed" href="#"><i class="fa fa-angle-left fa-2x"></i></a>
      <div v-for="effect in effects" class="effect" :class="effectClasses(effect)" @click="effectSelected(effect)"><span>{{effect.substring(0, 1).toUpperCase()}}</span></div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'effects',
  data () {
    return {
      expanded: false,
      effects: ['tonic', 'fifth', 'glissando', 'strings'],
      activeEffect: 'glissando'
    }
  },
  props: ['expandChange'],
  computed: {
    ...mapGetters(['effect'])
  },
  watch: {
    expanded () {
      if (this.expandChange) {
        if (!this.expanded) {
          const self = this
          setTimeout(() => { self.expandChange(self.expanded) }, 700)
        } else {
          this.expandChange(this.expanded)
        }
      }
    }
  },
  methods: {
    ...mapActions(['setEffect']),
    effectClasses (effect) {
      var classes = [effect]
      if (this.effect === effect) {
        classes.push('current')
      }
      return classes
    },
    effectsPressed () {
      this.expanded = !this.expanded
    },
    effectSelected (effect) {
      if (this.expanded) {
        this.setEffect(effect)
      } else {
        this.expanded = true
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../static/less/flex';
@import '../../static/less/common';

.effects {
  display: flex;
  flex-direction: row;
  align-items: center;
  a {
    color: white;
    margin-right: 10px;
  }
  i {
    transition: transform 0.7s;
  }
  &.expanded {
    .effect {
      display: flex;
      animation: slide-on 0.7s forwards;
    }
    i {
      transform: rotate(180deg) scale(1.4);
      padding-left: 10px;
    }
  }
  &:not(.expanded) {
    .effect:not(.current) {
      animation: slide-off 0.7s forwards;
    }
  }
  .effect {
    // display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 40px;
    width: 40px;
    margin-right: 10px;
    border-radius: 25px;
    border: solid 2px transparent;
    color: white;
    font-size: 18px;
    font-weight: bold;
    cursor: pointer;
    &.tonic {
      background-color: @color-tonic;
    }
    &.fifth {
      background-color: @color-fifth;
    }
    &.glissando {
      background-color: @color-blue;
    }
    &.strings {
      background-color: #ff9911;
    }
  }
  .effect.current {
    display: flex;
    border: solid 2px white !important;
    font-size: 22px;
    text-shadow: 0px 0px 3px black;
  }
}

@keyframes slide-off {
  0%   { max-width: 100px; opacity:1.0; }
  100% { max-width: 0px; border: none; margin: 0; opacity:0.0; }
}
@keyframes slide-on {
  0%   { max-width: 0px; margin: 0; opacity:0.0; }
  100% { max-width: 100px; opacity:1.0; }
}
</style>



